<template>
    <div class="search-item">
        <!-- 搜索栏 -->
        <div>
            <i class="iconfont icon-xiaoxi"></i>
        </div>
        <div class="search-input" @click="getSearchPage">
            <i class="iconfont icon-sousuo"></i>
            <span>{{keyword}}</span>
        </div>
        <div>
            <i class="iconfont icon-gouwuche"></i>
        </div>
    </div>
</template>


<script>
import { mapGetters } from 'vuex';

export default {
    computed:{
        ...mapGetters([
            "keyword"
        ])
    },
    methods:{
        getSearchPage(){
            this.$store.dispatch("getSearch");
        }
    }
}
</script>


<style lang="less" scoped>
.search-item{
    padding: 0.15rem;
    display: flex;
    justify-content: space-around;
    align-content: center;
    background: white;
    color: #aaa;
    .search-input{
        width: 75%;
        background-color: #eaeaea;
        border-radius: 0.05rem;
        padding: 0.05rem 0.1rem;
        .icon-sousuo{
            font-size: 20px;
            margin-top: 0.1rem;
        }
        span{
            margin-left: 0.1rem;
            font-size: 0.2rem;
        }
    }
    
}
</style>



