<template>
    <div class="item-cheaper">
        <!-- 超实惠 促销直达 -->
         <p class="title">超实惠－促销直达</p>
         <div class="cheaper-sale">
            <div v-for="(item, i) in soCheaper" :key="i" :class="'item'+i" class="div-float">
                <p class="item-title">{{item.title}}</p>
                <p class="item-desc">{{item.viceTitle|myDesc}}</p>
                <div class="item-img">
                    <img :src="item.image">
                </div>
            </div>
         </div>
    </div>
</template>


<script>
import {mapGetters} from "vuex"
export default {
    computed:{
        ...mapGetters([
            "soCheaper"
        ])
    },
    filters:{
        myDesc:function(data){
            //console.log(data.length);
            return data.substr(1,data.length-2);
        }
    }
}
</script>


<style lang="less" scoped>
.item-cheaper{
    background: white;
    margin-top: 0.2rem;
    .title{
        font-size: 0.23rem;
        padding: 0.15rem 0.2rem;
        border-bottom: 1px solid #ddd;
        color: #444;
    }
    .cheaper-sale{
        width: 100%;
        height: 5.2rem;
        font-size: 0.24rem;
        overflow: hidden;
        .item-desc{
            text-align: center;
            color: #fb3c64;
             font-size: 0.2rem;
        }
        .div-float{
            float: left;
        }
        .item0{
            width: 50%;
            height: 2.8rem;
            box-sizing: border-box;
            border-right: 1px solid #dfdfdf;
            border-bottom: 1px solid #dfdfdf;
            .item-title{
                text-align: center;
                margin-top: 0.3rem;
            }
            img{
                width: 50%;
                display: block;
                text-align: center;
            }
        }
        .item1,.item2{
            width: 50%;
            height: 1.4rem;
            box-sizing: border-box;
            border-bottom: 1px solid #dfdfdf;
            .item-title{
                margin-top: 0.3rem;
            }
            .item-desc{
                text-align: left;
            }
            p{
                margin-left: 0.1rem;
            }
            div{
                float: right;
            }
            img{
                width: 52%;
                position: relative;
                left: 45%;
                top: -0.8rem;
            }
        }
        .item3,.item4,.item5{
            width: 33%;
            height: 2.1rem;
            box-sizing: border-box;
            .item-title{
                text-align: center;
                margin-top: 0.2rem;
            }
            img{
                margin-top: 0.1rem;
                width: 60%;
                display: block;
                text-align: center;
            }
        }
        .item3,.item4{
            border-right: 1px solid #dfdfdf;
        }
    }
}
</style>



