import vue from "vue"

const state={
    comment:[],
    item:{},
    rateTags:[],
    total:"",
    averageScore:""
}

const getters={
    comment(){
        return state.comment;
    },
    item(){
        return state.item;
    },
    rateTags(){
        return state.rateTags;
    },
    averageScore(){
        return state.averageScore;
    },
    total(){
        return state.total;
    }
}

const actions={
    loadcomment({commit},iid){
        vue.http.jsonp("https://rate.mogujie.com/h5/rate/jsonp/x6.rate.ratelist/v1",{
            params:{
                itemId:iid
            }
        }).then(({data:{data}})=>{
            commit("loadcomment",data);
        })
    },
    saveDetail({commit},item){
        commit("saveDetail",item);
    }
}

const mutations={
    loadcomment(state,data){
        state.comment=data.list;
        state.rateTags=data.rateTags;
        state.total=data.total;
        state.averageScore=data.averageScore
        //console.log(data);
    },
    saveDetail(state,data){
        state.item=data;
    }
}

export default{
    state,
    getters,
    actions,
    mutations
}


