<template>
    <div>
         <!-- 头部 -->
        <div class="search-item">
            <!-- 搜索栏 -->
            <div @click="$router.go(-1)">
                <i class="iconfont icon-xiangzuojiantou"></i>
            </div>
            <div class="search-input">
                <p>购物车</p>
            </div>
            <div class="edit-div">
                <i class="iconfont icon-xiaoxi"></i>
                <p class="edit">编辑</p>
            </div>
        </div>
        <!-- 内容 -->
        <div class="container" v-if="flag">
            <div class="img">
                <i class="iconfont icon-gouwuche"></i>
            </div>
            <p>购物车还是空的哦～</p>
            <div class="btn">
                <router-link to="/"> 去逛逛</router-link>
            </div>
        </div>
        <!-- 购物车 -->
        <div class="cart-container"  v-if="!flag">
            <div class="item" v-for="(v,i) in cartItem" :key="i">
                <div class="item-img">
                    <img :src="v.img" alt="">
                </div>
                <div class="item-intro">
                    <p>{{v.title}}</p>
                    <div class="item-size">
                    </div>
                    <div class="price">
                        <p>价格:{{v.price}}元</p>
                    </div>
                </div>
            </div>
            <!-- footer -->
            <div class="footer">
                <p class="total-price">总价:<span>{{allMoney}}</span>元</p>
                <div class="comput-price">
                    <p>去结算</p>
                </div>
            </div>
        </div>

        
    </div>
</template>


<script>
import { mapGetters } from 'vuex';
export default {
    computed:{
        ...mapGetters([
            "cartItem",
            "flag",
            "allMoney"
        ])
    }

}
</script>


<style lang="less" scoped>
.search-item{
    padding: 0.07rem;
    display: flex;
    justify-content: space-around;
    align-content: center;
    background: white;
    color: #555;
    i{
        font-size: 0.32rem;
        line-height: 0.5rem;
    }
    .search-input{
        width: 75%;
        padding: 0.05rem 0.1rem;
        text-align: center;
        font-size: 0.3rem;
        
    }
    .edit-div{
            overflow: hidden;
            p{
                float: right;
                padding-top: 0.1rem;
                margin-right: 0.1rem;
            }
            i{
                float: right;
            }
        }
}
.container{
    width: 50%;
    padding-top: 3.2rem;
    .img{
        height: 2rem;
        width: 2rem;
        border-radius: 50%;
        overflow: hidden;
        text-align: center;
        line-height: 2rem;
        background: #d5cecb;
        color: aliceblue;
        i{
            font-size: 1.2rem;
        }
    }
    p{
        margin-top: 0.2rem;
        text-align: center;
        font-size: 0.23rem;
        color: #555;
    }
    .btn{
        font-size: 0.24rem;
        width: 45%;
        height: 0.5rem;
        background: #fb3c64;
        text-align: center;
        line-height: 0.5rem;
        border-radius: 0.1rem;
        margin-top: 0.2rem;
        a{
            text-decoration: none;
            color: white;
            font-weight: bold;
        }
    }
}
.cart-container{
    margin-top: 0.2rem;
    height: 10rem;
    .item{
        height: 2rem;
        background: white;
        margin-bottom: 0.2rem;
        display: flex;
        box-sizing: border-box;
        font-size: 0.2rem;
        padding: 0.1rem;
        .item-img{
            width: 25%;
            overflow: hidden;
            img{
                width: 100%;
            }
        }
        .item-intro{
            width: 75%;
            padding:0.1rem;
        }
        .item-size{
            margin: 0.2rem 0rem;
            overflow: hidden;
            p{
                float: left;
            }
        }
        .price{
            font-size: 0.25rem;
            color:#111;
        }
    }
}
.footer{
    width: 99.5%;
    height: 0.7rem;
    position: fixed;
    top: 93.6%;
    background: white;
    overflow: hidden;
    .total-price{
        float: left;
        font-size: 0.24rem;
        margin: 0.1rem 0.2rem;
        span{
            font-size: 0.4rem;
            color: #fb3c64;
            padding-left: 0.2rem;
        }
    }
    .comput-price{
        float: right;
        width: 1.5rem;
        height: 0.5rem;
        text-align: center;
        margin: 0.1rem;
        background: #fb3c64;
        border-radius: 5px;
        line-height: 0.5rem;
        color: white;
        font-size: 0.25rem;
        font-weight: bold;
    }
}
</style>



