<template>
    <div>
        <!-- 分类页 -->
        <SearchItem></SearchItem>
        <div class="container">
            <LeftComponent></LeftComponent>
            <RightComponent></RightComponent>
        </div>
        <BottonNav></BottonNav>
    </div>
</template>


<script>
import SearchItem from "../Template/SearchItem/SearchItem"
import LeftComponent from "./LeftComponent/LeftComponent"
import RightComponent from "./RightComponent/RightComponent"
import BottonNav from "../../BottonNav/BottonNav"

export default {
    components:{
        SearchItem,
        LeftComponent,
        RightComponent,
        BottonNav
    }
  
}
</script>


<style lang="less" scoped>
.container{
    display: flex;
    box-sizing: border-box;
    overflow: hidden;
}
</style>



