<template>
    <div class="detail-container">
        <div @click="$router.go(-1)" class="jiantou">
            <i class="iconfont icon-xiangzuojiantou"></i>
        </div>
        <div class="bodyDetail">
            <div class="imgBox">
                <img :src="msg.img">
            </div>
            <div class="detail">
                <p class="intro">{{msg.title}}</p>
                <div class="price-into">
                    <p class="nowPrice">¥<span class="price-now">{{msg.price}}</span></p>
                    <p class="price-old">¥{{msg.orgPrice}}</p>
                </div>
                <p class="camp-money">免邮费</p>
            </div>
        </div>

        <div class="comments">
            <div class="total">
                <p>买家评价{{averageScore}}  ｜销售{{total}}</p>
            </div>
            <div class="tabs">
                <span class="tab" v-for="(v,i) in rateTags" v-if="i!=0" :key="i" >{{v.value}}</span>
            </div>
            <div class="comment">
                <div class="one-comment" v-for="(v,i) in comment" :key="i">
                    <div class="touxiang">
                        <div class="img-div">
                            <img :src="v.user.avatar" alt="">
                        </div>
                        <p class="uersname">{{v.user.uname}}</p>
                    </div>
                    <div class="user-content">
                        <p >{{v.content}}</p>
                    </div>
                    <div>
                        <p class="time">{{v.time}}</p>
                    </div>
                </div>
            </div>
        </div>

        <div class="foot-nav">
            <div class="shoucang">
                <i class="iconfont icon-star"></i> 
                <p>收藏</p>
            </div>
            <div class="addShop">
                <i class="iconfont icon-gouwuche"></i> 
                <p>加购</p>
            </div>
            <div class="purchart" @click="nowBuy(msg)">
                <p>立即购买</p>
            </div>
        </div>
    </div>
</template>


<script>
import { mapGetters } from 'vuex';

export default {
    data(){
        return{
            msg:{}
        }
    },
    computed:{
        ...mapGetters([
            "comment",
            "item",
            "rateTags",
            "total",
            "averageScore"
        ])
    },
    mounted(){
        this.msg=this.$route.params.dataObj;
        this.$store.dispatch("saveDetail",this.msg);
        //console.log(this.msg);
        this.$store.dispatch("loadcomment",this.msg.iid);
    },
    methods:{
        nowBuy(obj){
            this.$store.dispatch("nowBuy",obj);
        }
    }
};
</script>


<style lang="less" scoped>
.jiantou{
    margin-top: 0.1rem;
    display: inline-block;
    width: 35px;
    height: 35px;
    position: absolute;
    font-weight: bold;
    color: white;
}
.bodyDetail{
    width: 99%;
    background: white;
    overflow: hidden;
    .imgBox{
        width: 100%;
        height: 8.3rem;
        overflow: hidden;
        img{
            width: 100%;
        }
    }
    .detail{
        font-size: 0.26rem;
        padding:0.2rem 0.2rem;
        .price-into{
            overflow: hidden;
        }
        .intro{
            width: 100%;
            height: 0.8rem;

            overflow: hidden;
            margin-bottom: 0.1rem;
        }
        .nowPrice{
            display: inline-block;
            float:left;
        }
        .price-now{
            font-size: 0.6rem;
        }
        .price-old{
            font-size: 0.2rem;
            float: left;
            margin: 0.2rem;
            text-decoration: line-through;
        }
        .camp-money{
            color: #777;
            font-size: 0.21rem;
            margin:0.13rem 0rem;
        }
    }
}
.comments{
    margin-top: 0.2rem;
    width: 100%;
    background: white;
    padding: 0.2rem;
    box-sizing: border-box;
    margin-bottom: 0.8rem;
    .total{
        width: 100%;
        height: 0.3rem;
        font-size: 0.22rem;
    }
    .tabs{
        margin-top: 0.15rem;
        height: 1.2rem;
        overflow: hidden;
        .tab{
            background: #fee1ea;
            padding: 0.06rem;
            color: #fb2979;
            margin: 0.1rem 0.1rem; 
            display: inline-block;
            border-radius: 0.2rem;
        }
    }
    .comment{
        margin-top: 0.1rem;
        .one-comment{
            border-bottom: 1px solid #ccc;
            padding-bottom: 0.2rem;
            .touxiang{
                height: 0.55rem;
                border: 1px solid white;
                margin-top: 0.2rem;
            }
            .img-div{
                width: 0.5rem;
                height: 0.5rem;
                border: 1px solid red;
                border-radius: 50%;
                overflow: hidden;
                float: left;
                img{
                    width: 100%;
                }
            }
            .uersname{
                margin: 0.15rem;
                padding-left: 0.6rem;
            }
            .user-content{
                color: #555;
                font-size: 0.22rem;
                padding: 0.1rem 0.2rem;
                overflow: hidden;
            }
            .time{
                color: #555;
                font-size: 0.18rem;
                padding: 0.07rem 0.2rem;
            }
        }
    }
}
.foot-nav{
    width: 100%;
    height: 0.87rem;
    background: white;
    position: fixed;
    z-index: 100;
    top:92.5%;
    overflow: hidden;
    .shoucang,.addShop,{
        color: #666;
        float: left;
        border-right: 1px solid #ccc;
        padding: 0.085rem 0.5rem 0rem 0.5rem;
    }
    .purchart{
        width: 54%;
        float: left;
        background: #fb2979;
        font-size: 0.35rem;
        padding-left: 0.03rem;
        p{
            color: white;
            font-weight: bold;
            padding-left: 1rem;
            margin: 0.2rem 0rem 0.2rem 0rem;
        }
    }
}
</style>



