    width: 100%;
    height: 7.7rem;
    background: white;
    .hot-title{
        background: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAATkAAAAKAgMAAACyFsHVAAAADFBMVEVMaXEzMzMzMzP///8xqFqyAAAAAnRSTlMAtlSR24EAAAABYktHRAMRDEzyAAAAQElEQVQoz2NgoDkQIVZhAHHKMok0jnEJccpWORBnHttK4pStmkCceVJYLV6FCkg1TwtVewPt/Uvt+KB6eqEIAACpih6NjokEAgAAAABJRU5ErkJggg==") no-repeat center center;
        background-size: 43% 12%;
        height: 0.75rem;
        line-height: 0.7rem;
        text-align: center;
        font-size: 0.26rem;
    }
    .container{
        width: 100%;
        height: 4rem;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        .item{
            width: 21%;
            height: 1.7rem;
            img{
                width: 90%;
                display: block;
                text-align: center;
            }
            p{
                color: #354b58;
                font-size: 0.2rem;
                text-align: center;
            }
        }
    }
}
</style>



