<template>
    <div class="you-like">
        <p class="like－title">猜你喜欢</p>
        <div class="container">
            <div class="item" v-for="(item,i) in youLike" :key="i">
                <div class="img-div">
                    <img :src="item.showLarge.img">
                </div>
                <div class="tag-div">
                    <div class="tag-label"><span v-for="(v,i) in item.props" :key="'p'+i">{{v}}</span></div>
                </div>
                <div class="price-div">
                    <p class="price"><b>{{item.price}}</b></p>
                    <p class="like-icon"></p>
                    <p class="hotnum">{{item.sale}}</p>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import {mapGetters} from "vuex"

export default {
    mounted(){
        this.$store.dispatch("getLikeInfo");
    },
    computed:{
        ...mapGetters([
            "youLike"
        ])
    }
  
}
</script>


<style lang="less" scoped>
.you-like{
    width: 100%;
    .container{
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 10rem;
        .item{
            width: 47%;
            height: 5.2rem;
            background: white;
            margin-bottom: 0.2rem;
            .img-div{
                width: 100%;
                height: 3.9rem;
                img{
                    width: 100%;
                    height: 100%;
                }
            }
            .tag-div{
                width: 100%;
                margin-top: 0.1rem;
                height: 0.62rem;
                display: flex;
                color: #485c66;
                font-size: 0.2rem;
                overflow: hidden;
                .tag-label{
                    margin: 0rem 0.1rem;
                }
                span{
                    background: #ebf0f4;
                    padding: 0rem 0.02rem;
                    margin: 0.01rem 0.1rem 0.01rem 0rem; 
                    display: inline-block;
                }
            }
            .price-div{
                font-size: 0.26rem;
                padding: 0.14rem 0.1rem;
                p{
                    display: inline-block;
                }
                .hotnum{
                    float: right;
                    margin: 0.06rem 0.08rem; 
                    font-size: 0.2rem;
                    color: #878787;
                }
                .like-icon{
                    margin: 0.08rem 0.05rem 0rem 0rem;
                    width: 0.2rem;
                    background: url("data:image/png;base64,") no-repeat;
                    background-size: 100% 100%;
                    height: 0.2rem;
                    float: right;
                }
                
            }
        }
    }
}
.like－title{
        background: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAATkAAAAKAgMAAACyFsHVAAAADFBMVEVMaXEzMzMzMzP///8xqFqyAAAAAnRSTlMAtlSR24EAAAABYktHRAMRDEzyAAAAQElEQVQoz2NgoDkQIVZhAHHKMok0jnEJccpWORBnHttK4pStmkCceVJYLV6FCkg1TwtVewPt/Uvt+KB6eqEIAACpih6NjokEAgAAAABJRU5ErkJggg==") no-repeat center center;
        background-size: 43% 12%;
        height: 1rem;
        line-height: 0.95rem;
        text-align: center;
        font-size: 0.26rem;
}
</style>



