<template>
    <div class="searchContainer">
        <div class="searchNav">
            <div @click="returnHome">
                <i class="iconfont icon-xiangzuojiantou"></i>
            </div>
            <div class="search-input" :class="pink?'pinkColor':'lossColor'">
                <input type="text" @focus="addPink" @blur="lossColor" v-model="search" :placeholder="keyword">
            </div>
            <p class="search" @click="doSearch">搜索</p>
        </div>
        <div class="history">
            <div>
                <i class="iconfont icon-sousuo"></i>
                <span>历史搜索</span>
                <i class="iconfont icon-lajixiang" @click="clearArr"></i>
            </div>
            <div class="tabs">
                <span class="tab-search" v-for="(v,i) in searchhistory" :key="i">{{v}}</span> 
                <span class="tab-search" v-if="flag">暂无历史搜素</span>                 
            </div>
        </div>
        <div class="history ">
            <div>
                <i class="iconfont icon-star"></i>
                <span>热门搜索</span>
            </div>
            <div class="tabs">
                <span v-for="(v,i) in hotWord" :style="'color:'+v.color" :key="i" class="tab-search">{{v.frontword}}</span>       
            </div>
        </div>
    </div>
</template>


<script>
import { mapGetters } from 'vuex';

export default {
    data(){
        return {
            pink:false,
            search:"",
            searchhistory:[],
            flag:true
        }
    },
    computed:{
        ...mapGetters([
            "hotWord",
            "keyword"
        ])
    },
    mounted(){
        this.loadhotWord();
    },
    methods:{
        returnHome(){
            this.$store.dispatch("returnHome")
        },
        addPink(){
            this.pink=true;          
        },
        lossColor(){
            this.pink=false;
        },
        loadhotWord(){
            this.$store.dispatch("loadhotWord");
        },
        doSearch(){
            if(this.search){
                this.searchhistory.push(this.search);
                this.flag=false;
                this.search="";
            }
        },
        clearArr(){
            this.searchhistory=[];
            this.flag=true;
        }
    }
}
</script>


<style lang="less" scoped>
.pinkColor{
    border: 1px solid #fb3c75;
}
.lossColor{
    border:1px solid #eaeaea
}
.searchContainer{
    width: 100%;
    height: 100%;
    position: fixed;
    background: white;
    z-index: 100;
    .searchNav{
        border-bottom: 1px solid #eaeaea;
        padding: 0.15rem 0.05rem;
        display: flex;
        justify-content: space-around;
        align-content: center;
        color: #aaa;    
        align-content: center;
        .icon-xiangzuojiantou{
            font-size: 0.38rem;
        }
        .search{
            line-height: 0.45rem;
            font-size: 0.22rem;
        }
        .search-input{
            width: 75%;
            border-radius: 0.1rem;
            padding: 0.05rem 0.1rem;
            input{
                color: #999;
                outline: none;
                width: 100%;
                font-size: 0.22rem;
            }
        }
    }
    .history{
        color: #999;
        font-size: 0.23rem;
        margin: 0.2rem 0rem;
        overflow: hidden;
        .icon-star{
            font-size: 0.26rem;
            margin-left: 0.2rem;
        }
        .icon-sousuo{
            font-size: 0.27rem;
            margin-left: 0.2rem;
        }
        .icon-lajixiang{
            font-size: 0.27rem;
            float: right;
            margin-right: 0.2rem;
        }
        .tabs{
            width: 100%;
            display: inline-block;
            padding-bottom: 0.1rem;
            overflow: hidden;
            .tab-search{
                margin: 0.1rem;
                border:1px solid #eaeaea;
                border-radius: 0.05rem;
                padding:0.05rem 0.2rem;
                display: inline-block;
                margin: 0.2rem 0.05rem 0.05rem 0.2rem; 
            }
        }
    }
}
</style>



