import Vue from "vue"

const state={
    type:[],
    showtype:[],
    bannerimg:[],
    sortFilter:[],
    wall:{},
    tempKey:""
}

const getters={
    type(){
        return state.type;
    },
    showtype(){
        return state.showtype;
    },
    bannerimg(){
        return state.bannerimg;
    },
    sortFilter(){
        return state.sortFilter;
    },
    tempKey(){
        return state.tempKey;
    },
    wall(){
        return state.wall;
    }
}

const actions={
    // 获取分类信息
    loadType({commit}){
        Vue.http.jsonp("https://mce.mogucdn.com/jsonp/multiget/3",{
            params:{
               pids:"41789"
            }
        }).then(({data:{data}})=>{
            //console.log(data[41789].list);
            commit("loadType",data[41789].list);
        })
    },
    // 根据id获取右边信息
    loadDate({commit},type="41888"){
        //console.log("仓库中的:"+type)
        Vue.http.jsonp("https://mce.mogujie.com/jsonp/makeup/3",{
            params:{
                pid:type,
            }
        }).then(({body:{data}})=>{
            //console.log(data);
            commit("loadDate",data);
        })
    },
    //获取右边具体信息
    getkindinfo({commit},params=
       {
           cKey:"h5-cube",
           fcid:"10062603",
           sort:""
        }){
        Vue.http.jsonp("https://list.mogujie.com/search",{
            params
        }).then(({data:{result}})=>{
            //console.log(result);
            commit("getkindinfo",result)
        })
    }
}

const mutations={
    loadType(state,data){
        state.type=data;
    },
    loadDate(state,data){
        state.showtype=data.categoryNavigation.list;
        if(!data.bannerHead){
            state.bannerimg=[];
        }else{
            state.bannerimg=data.bannerHead.list
        }     
    },
    getkindinfo(state,data){
        state.sortFilter=data.sortFilter;
        state.wall=data.wall.docs;
        state.tempKey=data.param.fcid;
    }
}

export default{
    state,
    getters,
    actions,
    mutations
};


