import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import AboutMeComponent from "../components/BodyContent/AboutMeComponent/AboutMeComponent.vue"
import CartComponent from "../components/BodyContent/CartComponent/CartComponent.vue"
import HomeComponent from "../components/BodyContent/HomeComponent/HomeComponent.vue"
import TypeComponent from "../components/BodyContent/TypeComponent/TypeComponent.vue"
import DetailInfo from "../components/BodyContent/Template/DetailInfo/DetailInfo.vue"

export default new Router({
  mode:"history",
  routes: [
    {
      path: '/',
      name: 'home',
      component:HomeComponent
    },
    {
      path: '/type',
      name: 'type',
      component:TypeComponent
    },
    {
      path: '/cart',
      name: 'cart',
      component:CartComponent
    },
    {
      path: '/aboutme',
      name: 'aboutme',
      component:AboutMeComponent
    },
    {
      path:"/detail",
      name:"detail",
      component:DetailInfo
    }
  ]
})



