<template>
    <div class="left-scroll">
        <div class="scroll">
            <div :id="v.maitKey" class="label-div" :class="activeIndex==i?'div-acitve':'no-active'" v-for="(v,i) in type" :key="i" @click="getInfo(v.maitKey,i,v.miniWallkey)">
                <p>{{v.title}}</p>
            </div>
        </div>
    </div>
</template>


<script>
import { mapGetters } from "vuex"
import vue from "vue"

export default {
    data(){
        return{
            typenow:"",
            activeIndex:-1,
        }
    },
    mounted(){
        this.$store.dispatch("loadType");
        this.getInfo("41888");
        this.activeIndex=0;
    },
    computed:{
        ...mapGetters([
            "type"
        ])
    },
    methods:{
        // 根据ID获取相应数据
        getInfo(type,i,miniWallkey){
           this.$store.dispatch("loadDate",type);
           this.loadProject(miniWallkey);
           this.activeIndex=i;
        },
        loadProject(miniWallkey="10062603"){
            let param={
                cKey:"h5-cube",
                fcid:miniWallkey,
                sort:""
            }
            this.$store.dispatch("getkindinfo",param);
        }
    }

}
</script>


<style lang="less" scoped>
.left-scroll{
    width: 26%;
    height: 9.75rem;
    font-size: 0.23rem;
    margin-bottom: 1rem;
    background: #f1f1f1;
    overflow-y: auto;
    color: #535353;
    .label-div{
        width: 100%;
        padding: 0.22rem 0rem;
    }
    p{
        text-align: center;
    }
}
.div-acitve{
    color: #fb3a64;
    background: white;
    font-weight: bold;
    p{
        border-left: 4px solid #fb3a64;
    }
}
.no-active{
    color: #575757;
    background: #f4f4f4;
    font-weight: normal;
    p{
        border-left: 0px;
    }
}
</style>



