import Vue from "vue"

const state={
    searchFlag:false,
    hotWord:{},
    keyword:""
}

const getters={
    searchFlag(){
        return state.searchFlag;
    },
    hotWord(){
        return state.hotWord;
    },
    keyword(){
        return state.keyword;
    }
}

const actions={
    getSearch({commit}){
        commit("getSearch")
    },
    returnHome({commit}){
        commit("returnHome")
    },
    loadhotWord({commit}){
        Vue.http.jsonp("https://list.mogujie.com/module/mget?code=sketch%2ChotWord"
        ).then(({data:{data}})=>{
            commit("loadhotWord",data);
            //console.log(data);
        })
    }
}

const mutations={
    getSearch(state){
        state.searchFlag=true;
    },
    returnHome(state){
        state.searchFlag=false;
    },
    loadhotWord(state,data){
        state.hotWord=data.hotWord.data;
        state.keyword=data.sketch.data.query;
        //console.log(state.keyword);
        //console.log(data.hotWord.data);
    }
}

export default {
    state,
    getters,
    actions,
    mutations
}


