<template>
    <div class="right-scroll">
        <!-- 右边布局 -->
        <!-- 上部分 -->
        <div>
            <div class="container-top">
                <!-- 小图标 -->
                <div class="banner" v-if='bannerimg.length>0'>
                    <img :src="bannerimg[0].image">
                </div>
                <div class="item-comp" v-for="(v,i) in showtype" :key="i">
                    <div class="img"><img :src="v.image"></div>
                    <p>{{v.title}}</p>
                </div>
            </div>
            <div class="next-content">
                <div class="sortFilter">
                    <p class="sortstyle" v-for="(v,i) in sortFilter" :key="i" :class="i==activeFlag?'active':''" @click="loadWallInfo(i,v.sortKey,tempKey)">{{v.title}}</p>
                </div>
                <!-- 商品展示 -->
                <div class="wall">
                   <div class="wallitem" @click="getDetail(v)" v-for="(v,i) in wall" :key="i" >
                       <div class="wallimg">
                           <img :src="v.img" alt="">
                            <p class="wall-sall">已售{{v.sale}}件</p>
                       </div>
                       <div class="details">
                           <p class="p-intro">{{v.title}}</p>
                           <p class="wallprice">¥{{v.price}}</p>
                           <p class="like-icon"></p>
                           <p class="like-num">{{v.cfav}}</p>
                       </div>
                   </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import {mapGetters} from "vuex"

export default {
    data(){
        return {
            activeFlag:-1
        }
    },
    computed:{
        ...mapGetters([
            "showtype",
            "bannerimg",
            "sortFilter",
            "tempKey",
            "wall"
        ])
    },
    created(){
        this.activeFlag=0;
    },
    methods:{
        loadWallInfo(i,sortKey,tempKey){
            console.log(tempKey);
            this.loadProject(tempKey,sortKey);
            this.activeFlag=i;
        },
        loadProject(miniWallkey,type){
            let param={
                cKey:"h5-cube",
                fcid:miniWallkey,
                sort:type
            }
            this.$store.dispatch("getkindinfo",param);
        },
        getDetail(v){
            this.$router.push({
                name:"detail",
                params:{
                    dataObj:v
                }
            })
        }
    }
}
</script>


<style lang="less" scoped>
.right-scroll{
    width: 75%;
    height: 9.75rem;
    overflow-y: auto;
    color: #535353;
    .container-top{
        overflow: hidden;
        background: white;
        .banner{
            width: 100%;
            height: 1.5rem;
            padding: 0.1rem 0.1rem;
            img{
                width: 100%;
            }
        }
        .item-comp{
            width: 25.5%;
            height: 1.5rem;
            margin: 0.25rem 0.17rem;
            text-align: center; 
            float: left; 
            .img{
                width: 100%;
                height: 1.2rem;
                background: #dfdfdf;
            }
            p{
                font-size: 0.2rem;
                margin: 0.08rem 0rem;
                text-overflow:ellipsis;  
                white-space: nowrap;  
            }
            img{
                width: 100%;
            }
        }
    }
    .next-content{
        background: white;
        font-size: 0.23rem;
        width: 100%;
        margin-top: 0.1rem;
        .sortFilter{
            display: flex;
            text-align: center;
            padding: 0.13rem 0rem;
            border-bottom: 1px solid #ccc;
            p{
                width: 100%;
                border-right: 2px solid #ccc
            }
        }
    }
    .wall{
        display: flex;
        flex-wrap: wrap;
        box-sizing: border-box;
        // background: white;
        .wallitem{
            margin-top: 0.1rem;
            width: 46%;
            height: 3.7rem;
            .wallimg{
                width: 100%;
                height: 2.9rem;
                overflow: hidden;
                img{
                    width: 100%;
                    height: 3.3rem;
                }
                .wall-sall{
                    font-size: 0.18rem;
                    position: relative;
                    z-index: 2;
                    top: -0.9rem;
                    background:linear-gradient(to right,rgba(0,0,0,0.7),rgba(255,255,255,0.1));
                    color:white;
                    height: 0.26rem;
                    padding: 0.05rem 0.15rem;
                }
            }     
        }
        .details{
            font-size: 0.2rem;
            .p-intro{
                width: 96%;
                height: 0.3rem;
                color: black;
                margin: 0.05rem 0.1rem;
                word-wrap: none;
                overflow: hidden;
                text-overflow: ellipsis;
            }
            .wallprice{
                color: #fb3c75;
                font-size: 0.24rem;
                font-weight: bold;
                margin-left: 0.1rem;
                display: inline-block;
            }
            .like-icon{
                float: right;
                margin: 0.06rem 0.05rem 0rem 0.08rem;
                width: 0.2rem;
                background: url("data:image/png;base64,") no-repeat;
                background-size: 100% 100%;
                height: 0.2rem;
                float: right;
            }
            .like-num{
                float: right;
                font-size: 0.16rem;
                margin-top: 0.07rem;
            }
        }
    }
}
.active{
    color: #fb3c75;
    font-weight: bold;
}
</style>



