<template>
  <div id="app">
      <!-- <BodyContent></BodyContent> -->

        <Searchpage v-show="searchFlag"></Searchpage>
        <router-view></router-view>

        
  </div>
</template>

<script>

import Searchpage from "./components/BodyContent/Template/SearchPage/Searchpage"
import { mapGetters } from 'vuex';

export default {
    computed:{
      ...mapGetters([
        "searchFlag"
      ])
    }
    ,
    components:{
        Searchpage
    }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0 auto;
  padding: 0;
  max-width: 7.5rem;
  font-size: 0.14rem;
}
</style>



