import Vue from "vue"

const state={
    banner:[],
    homeIndex:[],
    soCheaper:[],
    hotMarket:[],
    youLike:[]
}

const getters={
    banner(){
        return state.banner;
    },
    homeIndex(){
        return state.homeIndex;
    },
    soCheaper(){
        return state.soCheaper;
    },
    hotMarket(){
        return state.hotMarket;
    },
    youLike(){
        return state.youLike;
    }
}

const actions={
    // 获取轮播图图片
    getBanner({commit}){
        Vue.http.jsonp("https://mce.mogucdn.com/jsonp/multiget/3",{
            params:{
               pids: "51822,51836,51833,106930"
            }
        }).then(({data:{data}})=>{
            //console.log(data[51833].list);
            commit("getBanner",data);
        }).catch(error=>{
            console.log("获取错误"+error);
        })
                
    },
    // 获取猜你喜欢的信息
    getLikeInfo({commit}){
        Vue.http.jsonp("https://list.mogujie.com/search",{
            params:{
                pid:"9750",
                sort:"pop"
            }
        }).then(({body:{result:{wall:{list}}}})=>{
            //console.log(list);
            commit("getLikeInfo",list)
        })
    }
}

const mutations={
    getBanner(state,data){
        state.banner=data[51822].list;
        state.homeIndex=data[106930].list;
        state.soCheaper=data[51833].list;
        state.hotMarket=data[51836].list;
    },
    getLikeInfo(state,data){
        state.youLike=data;
    }
}

export default{
    state,
    getters,
    actions,
    mutations
}


