const state={
    cartItem:[],
    flag:true,
    allMoney:0
}

const getters={
    cartItem(){
        return state.cartItem;
    },
    flag(){
        return state.flag;
    },
    allMoney(){
        return state.allMoney;
    }
}

const actions={
    nowBuy({commit},obj){
        commit("nowBuy",obj)
    }
}

const mutations={
    nowBuy(state,obj){
        state.allMoney=0;
        state.cartItem.push(obj);
        //console.log(state.cartItem)
        state.flag=false;
        state.cartItem.forEach(v => {
            state.allMoney+=v.price
        });
    }
}

export default{
    state,
    getters,
    actions,
    mutations
}


