<template>
    <div class="types-content">
        <!-- 第二部分 主要导航 -->
        <div class="item" v-for="(v,i) in homeIndex" :key="i">
            <div class="img-circle">
                <img :src="v.image">
            </div>
            <p>{{v.title}}</p>
        </div>
    </div>
</template>


<script>
import {mapGetters} from "vuex"

export default {
    computed:{
        ...mapGetters([
            "homeIndex"
        ])
    }
}
</script>


<style lang="less" scoped>
.types-content{
    color: #4d4d4d;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content: center;
    box-sizing: border-box;
    background: white;
    padding-top: 0.25rem;
    .item{
        width: 15%;
        height: 1.45rem;
        margin: 0rem 0.1rem;
        text-align: center;
        margin-bottom: 0.1rem;
        .img-circle{
            width: 100%;
            overflow: hidden;
            img{
                width: 100%;
            }
        }
        p{
            font-size: 0.20rem;
            margin-left: 0.05rem;
            color: #444;
        }
    }
}
</style>



